
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
   background: rgba(255, 255, 255, 0); 
  padding: 1rem;
  z-index: 999;
}


.logo img {
  max-width: 150px;
  cursor: pointer;
  z-index: 999;
}
.logo{
  z-index: 999;
}

.hamburger {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 20px;
  cursor: pointer;
}

.line {
  width: 100%;
  height: 2px;
  transition: transform 0.3s ease;
}

.active .line:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}

.active .line:nth-child(2) {
  opacity: 0;
}

.active .line:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}


.menu {
  display: flex;
  justify-content: space-around;
  flex-grow: 1;
  margin-left: 10rem;
  margin-right: 2rem;
  padding: 0 1rem;
}

.menu li {
  font-size: 17px;
   font-weight: 600;
  font-family: Arial, sans-serif;
  color: #898383;
  padding: 5px;
  border-radius: 5px;
  text-align: center;
  cursor: pointer;
  z-index: 999;
  transition: .3s ease-in-out;
}
.menu li:hover{
    color: cadetblue;
}


.buttons {
  display: flex;
  align-items: center;
  z-index: 999;
}

.buttons .booking {
    margin-left: 1rem;
    padding: 0.8rem 1.5rem; 
    border: 4px solid cadetblue; 
    cursor: pointer;
    color: cadetblue; 
    background-color: rgba(0, 0, 0, 0); 
    font-size: 18px; 
  }
  .buttons .book-now {
    margin-left: 1rem;
    padding: 1rem 1.7rem; 
    cursor: pointer;
    color: rgb(255, 255, 255); 
    background-color: cadetblue; 
    font-size: 18px; 
    margin-right: 5rem;
    border: none;
    transition: .3s ease-in-out;
  }
  .buttons .book-now:hover {
    background-color: rgb(116, 222, 226);
  }


