  .carousel_slide {
    height: 400px;
    width: 1170px;   
    display: flex; 
    align-items: center; 
    /* justify-content: center;  */
    margin-top: 34px;
    margin-left: 4rem;
    position: relative;
    overflow-y: scroll;
  }
  
  .image_cont {
    /* position: relative; */
    height: 244px;
    width: 174px;
    align-items: center;
    justify-content: center;
    margin: 50px 20px 50px 20px;
    position: relative;
    cursor: pointer;
  }
  
  .image_cont img {
    object-fit: cover;
    transition: filter 0.3s ease;
  }
  
  .overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); /* Centering the overlay */
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    z-index: 1;
    transition: opacity 0.3s ease;
  }
  
  .play-icon{
    width: 60px;
    height: 60px;
    font-size: 1.5rem;
    color: white;
    border-radius: 50%; /* To create a circular shape */
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.646);
    transform: rotate(90deg); /* Rotate the icon 90 degrees right */
  }

    
  .image_cont:hover img {
    filter: brightness(70%); /* Darkens the image on hover */
  }
  
  .image_cont:hover .overlay {
    opacity: 1; /* Shows the overlay on hover */
  }
  
  .image_cont_inside_img {
    width: 174px; 
    height: 244px; 
    border-radius: 10px;
    
  }
  
  
