/* For webkit-based browsers (Chrome, Safari) */
/* The '::-webkit-scrollbar' pseudo-element targets the scrollbar */
/* You can customize the appearance and size of the scrollbar thumb and track */
/* In this example, we hide the scrollbar entirely */

/* Hide the scrollbar track */
.carousel_slide::-webkit-scrollbar-track {
  background-color: transparent;
}

/* Hide the scrollbar thumb */
.carousel_slide::-webkit-scrollbar-thumb {
  background-color: transparent;
}

/* For Firefox */
/* The 'scrollbar-width' property is used to hide the scrollbar */
/* 'none' value hides the scrollbar entirely */

.carousel_slide {
  scrollbar-width: none;
}

/* For IE/Edge - this might not be supported in the future */
/* The '-ms-overflow-style' property is used to hide the scrollbar */
/* 'none' value hides the scrollbar entirely */

.carousel_slide {
  -ms-overflow-style: none;
}

/* To prevent Firefox from showing the scrollbar overlay briefly before hiding */
/* you can add this rule */

.carousel_slide {
  overflow: -moz-scrollbars-none;
}