
.hero-container { 
    overflow: hidden;
    display: flex;
    align-items: center;
    height: 100vh; 
    width: 100%;
  }
  
  .background-image {
    position: absolute;
    top: 0;
    right: 0;
    width: 60%; 
    height: 90%; 
    z-index: -1;
     filter: drop-shadow(-166px -80px 0px rgb(222, 233, 239)) drop-shadow(-100px -80px 0px rgba(255, 255, 255, 0.84)); 
  }
  .background-image::after{
        content: "";
        position: absolute;
        inset: 0px 0px 0px -37.5px;
        transform: translate3d(0px, 70%, 0px);
        opacity: 1;
        z-index: -1;
        animation: 2800ms ease 500ms 1 normal forwards running fade-up-sm;
  }
  
  .text-container {
    color: #5b5757;
    max-width: 40%; 
    padding: 20px;
    margin-left: 5%; 
    position: relative;
    top:-10%
  }
  
  .text-container h1 {
    font-size: 55px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .text-container p {
    font-size: 30px;
    line-height: 1.5;
    font-weight: lighter;
  }
  
  
  @media (max-width: 768px) {
    .text-container {
      max-width: 80%; 
      margin: 0 auto; 
    }
  }
  
.arrow {
    margin-top: 30px;
    font-size: 4rem;
    color: cadetblue;
    animation: bounceUpDown 1s infinite alternate; 
  }


  
  
  @keyframes bounceUpDown {
    0% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(50px);
    }
  }


  .vapour {
    position: absolute;
    left: 0;
    width: 90%;
    display: flex;
    z-index: 0;
    padding: 0 28px;
  }
  
  .vapour span {
    position: relative;
    bottom: 50px;
    display: block;
    min-width: 8px;
    height: 120px;
    background: #ffffff;
    border-radius: 50%;
    animation: animate 15s linear infinite;
    opacity: 0;
    filter: blur(8px);
    animation-delay: calc(var(--i) * 0.5s); 
    overflow-x: hidden;
  }
.vapour2 {
    position: absolute;
    right: 0px;
    display: flex;
    z-index: 0;
    padding: 0 28px;
    display: flex;
    align-items: flex-start;
  }
  
  .vapour2 span {
    position: absolute;
    left: 20%;
    bottom: 50px;
    display: block;
    margin: 0 15px 50px;
    width: 5px;
    height: 100px;
    background: #e02d2d;
    border-radius: 50%;
    animation: animate2 10s linear infinite;
    opacity: 0;
    filter: blur(8px);
    animation-delay: calc(var(--i) * 0.5s); 
  }

  @keyframes animate2 {
    0% {
      transform: translateY(-200px)scaleX(0);
      opacity: 1;
    }
    50% {
      transform: translateY(-100px)scaleX(50);
      opacity: 1;
    }
    100% {
      transform: translateY(0)scaleX(0);
      opacity: 0;
    }
  }
  
  
  @keyframes animate {
    0% {
      transform: translateY(-400px) scaleX(150); 
      opacity: 1;
    } 
    5% { 
      opacity: 1;
    }
    15%{
      opacity: 1;
    }  
    20% {
      transform: translateY(-200) scaleX(150); 
      opacity: 1; 
    }
   
    100% {
      transform: translateY(-100px) scaleX(10); 
      opacity: 0; 
    }
   } 

  

  