.fourth-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* width: 100%; */
    margin: 0 auto;
  }
  
  .fourth-left-section {
    flex: 1;
    padding: 20px;
    margin-left: 4rem;
    margin-top: 3rem;
    transition: all 0.5s; 
    cursor: pointer;
  }
  
  .fourth-right-section {
    display: flex;
    flex-direction: column;
    flex: 1;
    margin-right: 3rem;
    margin-top: -5rem;
    transition: all 0.5s; 
    cursor: pointer;
  }
  
 
  .fourth-left-section:hover {
    transform: translateY(-10px); 
  }
  
  .fourth-right-section:hover {
    transform: translateY(-10px); 
  }

  
  .fourth-image {
    width: 100%;
    max-width: 500px;
    max-height: 550px;
    object-fit: cover;
  }
  
  .fourth-text-container {
    margin-top: 20px;
  }
  
  .fourth-hotel-name {
    font-size: 40px;
    font-weight: bold;
    margin-bottom: 2rem;
    color: #666;
    line-height: 1.3;
    position: relative;
  }
  
  .fourth-price {
    font-size: 18px;
    color: #666;
    margin-bottom: 1rem;
  }
  
  .fourth-hotel-description {
    font-size: 22px;
    color: #666;
  }
  
  .fourth-right-text {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
    color: #666;
  }
  
  .fourth-right-image {
    width: 100%;
    max-width: 500px;
    max-height: 400px;
    margin-bottom: 20px;
    object-fit: cover;

  }
  
  .fourth-right-text-container {
    margin-top: 20px;
    text-align: left;

  }
  
  
  
  .fourth-container {
    padding: 20px;
  }
  
  .fourth-right-text {
    font-size: 3.5rem;
    margin-left: 0px;
    color: rgba(0, 0, 0, 0.746);
    margin-bottom: 1.5rem;
  }
  .fourth-right-p{
    font-size: 1.5rem;
    width: 80%;
    color: rgba(0, 0, 0, 0.746);
    text-align: left;
    line-height: 1.5;
    margin-bottom: 4rem;
  }
  
  .fourth-first-text{
    margin-left: 1rem;
    padding-bottom: calc(3.8224px + 0.2984vw);
    line-height: 1.25;
    transition: color 500ms ease 0s;
    opacity: .5;
    transition: opacity 0.5s ease, transform 0.5s ease; 
    will-change: opacity, transform;
    margin-bottom: 3rem;
}

.fourth-first-text.fade-in {
  opacity: 1;
  transform: translateY(-20px);
}

.fourth-left-arrow {
  font-size: 18px; 
  color: cadetblue;
  margin-left: 10px; 
  right: 10%;
  top: 30%;
  position: absolute;
  font-weight: lighter;
}


  